@import url(https://fonts.googleapis.com/css?family=Orbitron:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{font-size:2vh}:root *{margin:0;padding:0;box-sizing:border-box;font-family:"Orbitron", sans-serif}:root .App{text-align:center;color:#61dafb;background-color:#282c34;min-height:100vh;display:flex;align-items:center;justify-content:center;font-size:calc(10px + 2vmin)}:root .App .Config{border:5px solid #61dafb;padding:2rem 4rem;border-radius:25px}:root .App .Config .config-container{padding:0.5rem;font-size:2rem;border:5px solid #61dafb;background-color:white;border-radius:25px;margin:1rem 0;color:#282c34}:root .App .Config .config-container .plus,:root .App .Config .config-container .minus{color:red;font-size:4rem;cursor:pointer}:root .App .Config .config-container .plus:hover,:root .App .Config .config-container .minus:hover{color:green;transition-duration:200ms}:root .App .Clock{padding:4rem 2rem;border:5px solid #61dafb;border-radius:0 25px 25px 0;border-left:none}:root .App .Clock .clock-container{display:flex;flex-direction:column;justify-content:space-between;align-items:center}:root .App .Clock .clock-container .clockTitle{padding:1.5rem;font-size:3rem}:root .App .Clock .clock-container .time-left{min-width:20vw;color:#282c34;border:5px solid #61dafb;border-radius:25px;background-color:white;font-family:"Orbitron", sans-serif;font-size:4rem}:root .App .Clock .clock-container .control-buttons{margin-top:3rem}:root .App .Clock .clock-container #start_stop,:root .App .Clock .clock-container #reset{cursor:pointer;color:white;font-weight:700;font-size:2rem;background-color:#61dafb;padding:1rem 2rem;border:5px solid #61dafb;border-radius:25px}:root .App .Clock .clock-container #reset{margin-top:2rem;padding:1rem 4.5rem}:root .App .Clock .clock-container #reset:hover,:root .App .Clock .clock-container #start_stop:hover{background-color:red;transition-duration:200ms}:root .App .Clock .clock-container #reset:active,:root .App .Clock .clock-container #start_stop:active{transition-duration:100ms;background-color:green}@media only screen and (max-width: 600px){:root .App{flex-direction:column}:root .App .Config{border-radius:25px 25px 0 0;border-bottom:none}:root .App .Clock{border-radius:25px;border-left:5px solid #61dafb}}

